import React, { useEffect, useState } from 'react';
import '../styles/globalStyles.css';
import { useParams, useHistory } from 'react-router-dom';
import { actualizarUsuario, obtenerUsuario } from '../services/service';
import Swal from 'sweetalert';

const EditarAdmin = () => {
    const { id } = useParams();
    const history = useHistory();

    const [alerta, setAlerta] = useState(true);
    const [admin, setAdmin] = useState({
        displayName: '',
        phoneNumber: '',
        email: '',
    });

    const { displayName, phoneNumber, email } = admin;
    const handleInputChange = (e) => {
        setAdmin({
            ...admin,
            [e.target.name]: e.target.value
        });
        setAlerta(true);
    }

    useEffect(() => {
        obtenerAdmin();
    }, []);


    const obtenerAdmin = async () => {
        const res = await obtenerUsuario(id);
        let { data } = res;

        let { displayName, phoneNumber, email } = data;

        setAdmin({ displayName, phoneNumber, email });
    }

    const actualizarAdmin = async () => {
        if (displayName.trim().length > 0 && phoneNumber.trim().length > 0 && email.trim().length > 0 ) {
            const res = await actualizarUsuario(id, { displayName, phoneNumber  });
            Swal('Usuario Administrador Editado con Exito!', '', 'success').then((value) => {
                if (value) {
                    history.push('/usuarios');
                }
            });
        } else {
            setAlerta(false);
        }
    }

    return (
        <div className="bg-light align-middle">
            <div>
                <p className="h3 m-3 text-center">
                    Editar Administrador
                </p>
            </div>
            <div className="container h-100" style={{ height: '100vh' }}>

                <div className="row ">
                    <div className="col-12 col-sm-12 col-md-4"> </div>
                    <div className="col-12 col-sm-12 col-md-4 border p-3 bg-white">
                        <div>
                            <div className="text-center ">
                            </div>
                            <div className="form-group">
                                <label >Nombre</label>
                                <input name="displayName" value={displayName} onChange={handleInputChange} type="text" className={`form-control`} placeholder="Ingrese nombre" />
                            </div>
                            <div className="form-group">
                                <label > Teléfono </label>
                                <input name="phoneNumber" value={phoneNumber} onChange={handleInputChange} className={`form-control`} placeholder="Ingrese número de  teléfono " ></input>
                            </div>
                            <div className="form-group">
                                <label > Correo </label>
                                <input name="email" value={email} disabled onChange={handleInputChange} className={`form-control`} placeholder="Ingrese su Correo" ></input>
                            </div>
                            {
                                alerta == false && <div className="alert alert-danger is-invalid" role="alert">
                                    Todos los campos son requeridos
                                </div>
                            }
                            <br></br>
                            <button type="button" onClick={actualizarAdmin} className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color:'white'}}>Actualizar</button>
                        </div>
                    </div>
                    <div className="col-12 col-sm-12 col-md-4"> </div>

                </div>

            </div>


        </div>
    )
}

export default EditarAdmin
