import React, { useState } from 'react';
import '../styles/globalStyles.css';
import { useHistory } from 'react-router-dom';
import { agregarUsuario, subirImagen } from '../services/service';
import Swal from 'sweetalert2';

const AgregarAdmin = () => {
    const history = useHistory();
    const [imgSubida, setImgSubida] = useState(false);
    const [error, setError] = useState('');
    const [codigos, setCodigos] = useState({
        correo: 'auth/email-already-exists',
        numero: 'auth/invalid-phone-number',
        contrasenia: 'auth/invalid-password',
    });

    const [alerta, setAlerta] = useState(true);
    const [admins, setAdmins] = useState({
        displayName: '',
        phoneNumber: '',
        email: '',
        password: '',
        photoURL: '',
    });
    const { displayName, phoneNumber, email, password, photoURL } = admins;
    const { correo, contrasenia, numero } = codigos;

    const handleInputChange = (e) => {
        const { value } = e.target;

        setAdmins({
            ...admins,
            [e.target.name]: value
        });

        setAlerta(true);
    }

    const agregarAdministrador = async () => {

        if (displayName.trim().length > 0 && phoneNumber.trim().length > 0 && email.trim().length > 0 && password.trim().length > 0 && photoURL.trim().length > 0) {
            const response = await agregarUsuario(admins);
            const body = response.data;
            if (body.ok) {
                console.log(body);
                Swal.fire(
                    'Administrador Creado con Exito!',
                    'You clicked the button!',
                    'success'
                );
                history.push('/usuarios')

            } else {
                console.log(body.mensaje.code);
                setError(body.mensaje.code)

            }
        } else {
            setAlerta(false);
        }
    }

    const handleSutmitImage = async (e) => {
        console.log(e.target.files[0]);

        let formData = new FormData();
        formData.append('upload_preset', 'galeria');
        formData.append('file', e.target.files[0]);

        const res = await subirImagen(formData);
        const body = await res.json();

        setAdmins({
            ...admins,
            photoURL: body.url
        });
        setImgSubida(true);
    }


    return (
        <div className="bg-light align-middle">
            <div>
                <p className="h3 m-3 text-center">
                    Agregar Administrador
                </p>
            </div>
            <div className="container h-100" style={{ height: '100vh' }}>
                <div className="row ">
                    <div className="col-12 col-sm-12 col-md-4"> </div>
                    <div className="col-12 col-sm-12 col-md-4 border p-3 bg-white">
                        <div>
                            <div className="text-center ">
                            </div>
                            <div className="form-group">
                                <label >Nombre</label>
                                <input name="displayName" value={displayName} onChange={handleInputChange} type="name" className={`form-control`} placeholder="Ingrese nombre" />
                            </div>
                            <div className="form-group">
                            <label > Teléfono </label>
                                <input cols="5" name="phoneNumber" value={phoneNumber} onChange={handleInputChange} className={`form-control`} placeholder="Ingrese número de teléfono" ></input>
                                {
                                    numero == error && <div className="text-danger">Número de teléfono inválido</div>
                                }
                            </div>
                            <div className="form-group">
                            <label > Correo </label>
                                <input cols="5" name="email" value={email} onChange={handleInputChange} className={`form-control`} placeholder="Ingrese su Correo" ></input>
                                {
                                    correo == error && <div className="text-danger">La dirección de correo electrónico ya está siendo utilizada por otra cuenta.</div>
                                }
                            </div>
                            <div className="form-group">
                            <label > Contraseña</label>
                                <input cols="5" name="password" type= "password" value={password} onChange={handleInputChange} className={`form-control`} placeholder="Ingrese su contraseña" ></input>
                                {
                                    contrasenia == error && <div className="text-danger">La contraseña debe ser una cadena de al menos 6 caracteres.</div>
                                }
                            </div>
                            
                            <div className="custom-file">
                                <input onChange={handleSutmitImage} type="file" className="custom-file-input" id="customFileLang" lang="es" />
                                <label className="custom-file-label" for="customFileLang">Seleccionar Fotografía</label>
                                {
                                    imgSubida && <p>Imagen subida correctamente</p>
                                }
                            </div>
                            {
                                alerta == false && <div className="alert alert-danger is-invalid" role="alert">
                                    Todos los campos son requeridos
                                </div>
                            }
                            <br></br>
                            <button type="button" onClick={agregarAdministrador} className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color:'white'}}>Guardar</button>
                        </div>
                    </div>
                    <div className="col-12 col-sm-12 col-md-4"> </div>

                </div>

            </div>


        </div>
    )
}

export default AgregarAdmin
