import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { obtenerBrandsFirebase, eliminarBrandsFirebase, obtenerMarcaPorIdFirebase } from '../services/service';
import Swal from 'sweetalert2';

const Brands = () => {
    const noImage = 'https://upload.wikimedia.org/wikipedia/commons/1/14/No_Image_Available.jpg';
    const [brands, setBrands] = useState([]);
    const [brand, setBrand] = useState({
        name: '',
        description: '',
    });
    const { name, description } = brand;

    useEffect(() => {
        obtenerBrands()
    }, [])

    const obtenerBrands = async () => {
        const temp = [];
        const resp = await obtenerBrandsFirebase();

        resp.forEach(element => {
            temp.push({
                id: element.id,
                ...element.data()
            })
        });
        setBrands(temp);
    }
    const seleccionarBrands = async (id) => {
        const resp = await obtenerMarcaPorIdFirebase(id);

        let data = {
            ...resp.data()
        }

        let { name, description } = data;

        setBrand({
            name,
            description
        });

    }

    const eliminarBrands = async (IdBrands) => {
        Swal.fire({
            title: '¿Está seguro?',
            text: "No podrás revertir esto",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: '¡Si, borralo!'
        }).then(async (result) => {
            if (result.isConfirmed) {
                await eliminarBrandsFirebase(IdBrands);
                obtenerBrands();
                Swal.fire(
                    'Eliminado',
                    'El Producto ha sido eliminado.',
                    'success'
                )
            }
        }
        )
    }



    return (
        <div className="container ">
            <h3 className="mt-2 mb-2 text-center">
                Listado de Marcas
            </h3>

            <table className="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">Imagen</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Descripción</th>
                        <th scope="col">Editar</th>
                        <th scope="col">Eliminar</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        brands.map((brand) => {
                            return (
                                <tr key={brand.id}>
                                    <th scope="row">
                                        <button onClick={() => seleccionarBrands(brand.id)} type="button" className="btn" data-toggle="modal" data-target="#exampleModal">
                                            <img src={brand.image || noImage} alt="img" className="rounded-circle" style={{ width: 50, height: 50 }} />
                                        </button>
                                    </th>
                                    <td>
                                        {brand.name}
                                    </td>
                                    <td>
                                        {brand.description}
                                    </td>

                                    <td>
                                        <Link to={`/editar-marca/${brand.id}`} className="btn btn-sm btn-success">Editar</Link>
                                    </td>
                                    <td>
                                        <button onClick={() => eliminarBrands(brand.id)} type="button" className="btn btn-sm btn-danger">Eliminar</button>
                                    </td>
                                </tr>

                            )
                        })
                    }

                </tbody>

            </table>

        </div>
    )
}
export default Brands
