import React from 'react';
import Navbar from '../components/Navbar';
import Sidebar from '../components/Sidebar';
import { Switch, Route, Redirect } from 'react-router-dom';
import Productos from './Productos';
import Categorias from './Categorias';
import Usuarios from './Usuarios';
import Nav from '../components/Nav';
import Ingresarproducto from '../components/CrearProducto';
import Crearproducto from '../components/CrearProducto';
import CrearCategoria from '../components/CrearCategoria';
import EditarProducto from './EditarProducto';
import CrearMarca from './CrearMarca';
import Marcas from './Marcas';
import EditarMarca from './EditarMarca';
import EditarCategoria from '../components/EditarCategoria';
import EditarAdmin from './EditarAdmin';
import AgregarAdmin from './AgregarAdmin';
import listarUsuarios from './listarUsuarios';
import Grafica from './Grafica';



const Dashboard = () => {
    return (
        <div className="sb-nav-fixed" >
            <Nav />

            <div id="layoutSidenav">
                <div id="layoutSidenav_nav">
                    <Navbar />
                </div>
                <div id="layoutSidenav_content" className="">
                    <main>
                        <div className="container-fluid px-4">

                            <Switch>
                                <Route exact path="/resumen" component={Grafica} />
                                <Route exact path="/usuarios" component={Usuarios} />
                                <Route exact path="/products" component={Productos} />
                                <Route exact path="/agregar-categoria" component={CrearCategoria} />
                                <Route exact path="/crear-producto" component={Crearproducto} />
                                <Route exact path="/categories" component={Categorias} />
                                <Route exact path="/editar-categorias/:id" component={EditarCategoria} />
                                <Route exact path="/editar-productos/:id" component={EditarProducto} />
                                <Route exact path="/crear-marca/" component={CrearMarca} />
                                <Route exact path="/brands" component={Marcas} />
                                <Route exact path="/editar-marca/:id" component={EditarMarca} />
                                <Route exact path="/crear-admin" component={AgregarAdmin} />
                                <Route exact path="/editar-admin/:id" component={EditarAdmin} />
                                <Route exact path="/listar-usuarios" component={listarUsuarios} />
                                <Redirect to="/resumen" />
                            </Switch>
                        </div>
                    </main>
                </div>
            </div>

        </div>
    )
}

export default Dashboard
