import React, { useEffect, useState } from 'react';
import '../styles/globalStyles.css';
import Swal from 'sweetalert';
import { useParams, useHistory } from 'react-router-dom';
import { obtenerCategoriaPorIdFirebase, actualizarCategoriaPorIdFirebase } from '../services/service';

const EditarCategoria = () => {

    const { id } = useParams();
    const history = useHistory();
    const [alerta, setAlerta] = useState(true);
    const [categoria, setCategoria] = useState({
        id: '',
        name: '',
        description: '',
    });

    const { name, description } = categoria;

    const handleInputChange = (e) => {
        setCategoria({
            ...categoria,
            [e.target.name]: e.target.value
        });
        setAlerta(true);
    }

    useEffect(() => {
        obtenerCategoria();
    }, []);


    const obtenerCategoria = async () => {
        const res = await obtenerCategoriaPorIdFirebase(id);

        let data = {
            ...res.data()
        }

        let { name, description } = data;

        setCategoria({ id, name, description });
    }

    const actualizar = async () => {
        if (name.trim().length > 0 && description.trim().length > 0) {
            const res = await actualizarCategoriaPorIdFirebase(id, { name, description });
            Swal('Categoría Editada con Exito!', '', 'success').then((value) => {
                if (value) {
                    history.push('/categories')
                }
            });
        } else {
            setAlerta(false);
        }

    }


    return (
        <div className="w-100 h-100 contenedor-100 bg-light ">
            <br></br>
            <div>
                <p className="h3 text-center">
                    Editar Categoría
                </p>
            </div>
            <br></br>
            <div className="d-flex justify-content-center ">
                <div className="d-flex align-items-center bg-white col-6 card">

                    <div className="row pb-5 pt-5">
                        <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                            <img className="img-fluid" alt="img" src="https://res.cloudinary.com/unahvs/image/upload/v1629397615/nuevas/EditarCategoria_rfihwd.png" />
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                            <div className="form-group ">
                                <label>Nombre</label>
                                <input name="name" type="nombre" value={name} onChange={handleInputChange} className="form-control" />
                            </div>
                            {
                                alerta == false && <div className="alert alert-danger is-invalid" role="alert">
                                    Todos los campos son requeridos
                                </div>
                            }
                            <div className="form-group">
                                <label>Descripción</label>
                                <textarea name="description" value={description} onChange={handleInputChange} className="form-control" rows=""></textarea>
                            </div>
                            <button type="button" onClick={actualizar} className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color:'white'}} >Guardar</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default EditarCategoria