export const subirImagen = async (imagen) => {
    try {
        const url = 'https://api.cloudinary.com/v1_1/unahvs/image/upload';
        const response = await fetch(url, {
            method: 'POST',
            body: imagen,
        });
        return response;
    } catch (error) {
        console.log(error)

        return error;
    }
}

