import React, { useEffect, useState } from 'react'
import { cambiarEstado, obtenerUsuariosNode } from '../services/service';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';

const ListarUsuarios = () => {

    const noImage = 'https://upload.wikimedia.org/wikipedia/commons/1/14/No_Image_Available.jpg';

    const [usuarios, setUsuarios] = useState([]);

    useEffect(() => {
        obtenerListaUsuarios();
    }, []);

    const cambiarEstadoCliente = async (uid) => {

        const resp = await cambiarEstado(uid);
        const { data } = resp;

        if (data.ok) {
            obtenerListaUsuarios();
        }
    }


    const obtenerListaUsuarios = async () => {

        let users = [];

        const response = await obtenerUsuariosNode();

        if (response.status === 200) {
            const { data } = response;

            if (data.length > 0) {
                data.forEach(element => {
                    if (element.email) {
                        let correo = element.email.split('@');
                        if (correo[1] !== 'allinone.com') {
                            users.push(element);
                        }
                    }
                });

            }
            setUsuarios(users);
        }
    }
    return (
        <div>
            <h3 className="mt-2 mb-2 mt-2 text-center">
                Clientes
            </h3>

            <table className="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">Avatar</th>
                        <th scope="col">Username</th>
                        <th scope="col">Email</th>
                        <th scope="col">Estado</th>

                    </tr>
                </thead>
                <tbody>
                    {
                        usuarios.map(user => {
                            return (
                                <tr key={user.uid}>
                                    <th scope="row">
                                        <img src={user.photoURL || noImage} alt="img" className="rounded-circle" style={{ width: 30, height: 30 }} />
                                    </th>
                                    <td>
                                        {user.displayName || 'No definido'}
                                    </td>
                                    <td>
                                        {user.email}
                                    </td>
                                    <td>
                                        {
                                            <button onClick={() => cambiarEstadoCliente(user.uid)} type="button" className={`btn btn-sm ${!user.disabled ? 'btn-danger' : 'btn-success'}`}>{!user.disabled ? 'Inhabilitar' : 'Habilitar'}</button>
                                        }
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>
        </div>
    )
}

export default ListarUsuarios;
