import React, { useEffect, useState } from 'react'
import { eliminarUsuarioNode, obtenerUsuariosNode } from '../services/service';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';

const Usuarios = () => {
    const noImage = 'https://upload.wikimedia.org/wikipedia/commons/1/14/No_Image_Available.jpg';

    const [usuarios, setUsuarios] = useState([]);

    useEffect(() => {
        obtenerListaUsuarios();
    }, []);

    const obtenerListaUsuarios = async () => {

        let users = [];

        const response = await obtenerUsuariosNode();

        if (response.status === 200) {
            const { data } = response;

            if (data.length > 0) {
                data.forEach(element => {
                    if (element.email) {
                        let correo = element.email.split('@');
                        if (correo[1] == 'allinone.com') {
                            users.push(element);
                        }
                    }
                });

            }
            setUsuarios(users);
        }
    }

    const eliminarUsuario = (uid) => {
        let uidActivo = localStorage.getItem("uid");

        if (uidActivo == uid) {
            Swal.fire(
                '',
                'El Usuario no se puede Eliminar',
                'warning'
            )
        } else {
            Swal.fire({
                title: '¿Está seguro?',
                text: "No podrás revertir esto",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: '¡Si, borralo!'
            }).then(async (result) => {
                if (result.isConfirmed) {
                    await eliminarUsuarioNode(uid);
                    obtenerListaUsuarios();
                    Swal.fire(
                        'Eliminado',
                        'El Usuario ha sido eliminado.',
                        'success'
                    )
                }
            })

        }

    }

    return (
        <div>

            <h3 className="mt-2 mb-2 text-center">
                Administradores Autorizados
            </h3>

            <table className="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">Avatar</th>
                        <th scope="col">Username</th>
                        <th scope="col">Email</th>
                        <th scope="col">Teléfono</th>
                        <th scope="col">Editar</th>
                        <th scope="col">Eliminar</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        usuarios.map(user => {
                            return (
                                <tr key={user.uid}>
                                    <th scope="row">

                                        <img src={user.photoURL || noImage} alt="img" className="rounded-circle" style={{ width: 30, height: 30 }} />

                                    </th>
                                    <td>
                                        {user.displayName}
                                    </td>
                                    <td>
                                        {user.email}
                                    </td>
                                    <td>
                                        {user.phoneNumber}
                                    </td>

                                    <td>
                                        <Link to={`/editar-admin/${user.uid}`} className="btn btn-sm btn-success">Editar</Link>
                                    </td>
                                    <td>
                                        <button onClick={() => eliminarUsuario(user.uid)} type="button" className="btn btn-sm btn-danger">Eliminar</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </table>
        </div>
    )
}

export default Usuarios
