import React, { useState } from 'react';
import '../styles/globalStyles.css';
import { useHistory } from 'react-router-dom';
import { guardarMarcaFirebase, subirImagen } from '../services/service';
import Swal from 'sweetalert';


const CrearMarca = () => {
    const [imgSubida, setImgSubida] = useState(false);
    const history = useHistory();
    const [alerta, setAlerta] = useState(true);
    const [brands, setbrands] = useState({
        name: '',
        description: '',
        image: '',
    });

    const { name, description, image } = brands;

    const handleInputChange = (e) => {
        const { value } = e.target;

        setbrands({
            ...brands,
            [e.target.name]: value
        });

        setAlerta(true);
    }

    const agregarMarca = async () => {

        //(name.trim().length == 0) ?  setValidateNombre(false) : setValidateNombre(true);
        //(description.trim().length == 0) ?  setValidateDescripcion(false) : setValidateDescripcion(true);

        if (name.trim().length > 0 && description.trim().length > 0 && image.trim().length > 0) {
            await guardarMarcaFirebase(brands);
            Swal('Marca Creada con Exito!', '', 'success').then((value) => {
                if (value) {
                    history.push('/brands');
                }
            });
           
        } else {
            setAlerta(false);
        }
    }

    const handleSutmitImage = async (e) => {
        console.log(e.target.files[0]);

        let formData = new FormData();
        formData.append('upload_preset', 'galeria');
        formData.append('file', e.target.files[0]);

        const res = await subirImagen(formData);
        const body = await res.json();

        setbrands({
            ...brands,
            image: body.url
        });
        setImgSubida(true);
    }


    return (
        <div className="bg-light align-middle">
            <br></br>
            <div>
                <p className="h3 text-center">
                    Agregar Nueva Marca
                </p>
            </div>
            <br></br>
            <div className="container h-100" style={{ height: '100vh' }}>

                <div className="row ">
                    <div className="col-12 col-sm-12 col-md-4"> </div>
                    <div className="col-12 col-sm-12 col-md-4 border p-3 bg-white">
                    <div>
                            <div className="text-center ">
                            </div>
                            <div className="form-group">
                                <label >Nombre</label>
                                <input name="name" value={name} onChange={handleInputChange} type="name" className={`form-control`} placeholder="Ingrese nombre de la Marca" />
                            </div>
                            <div className="form-group">
                                <label>Descripción</label>
                                <textarea cols="5" name="description" value={description} onChange={handleInputChange} className={`form-control`} placeholder="Ingrese una descripción" ></textarea>
                            </div>
                            <label>Imagen</label>
                            <div className="custom-file">
                                <input onChange={handleSutmitImage} type="file" className="custom-file-input" id="customFileLang" lang="es" />
                                <label className="custom-file-label" for="customFileLang">Seleccionar Archivo</label>
                                {
                                    imgSubida && <p>Imagen subida correctamente</p>
                                }
                            </div>
                            {
                                alerta == false && <div className="alert alert-danger is-invalid" role="alert">
                                    Todos los campos son requeridos
                                </div>
                            }
                            <br></br><br></br>
                            <button type="button" onClick={agregarMarca} className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color:'white'}}>Guardar</button>
                        </div>
                    </div>
                    <div className="col-12 col-sm-12 col-md-4"> </div>

                </div>

            </div>


        </div>
    )
}

export default CrearMarca
