import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { eliminarCategoriaFirebase, obtenerCategoriaFirebase } from '../services/service';
import Swal from 'sweetalert2'


const Categorias = () => {
    const [categorias, setCategoria] = useState([]);

    useEffect(() => {
        obtener()
    }, [])

    const obtener = async () => {
        const temp = [];
        const resp = await obtenerCategoriaFirebase();

        resp.forEach(element => {
            temp.push({
                id: element.id,
                ...element.data()
            })
        });
        setCategoria(temp);
    }

    const eliminarCategoria = async (IdCategoria) => {
        Swal.fire({
            title: '¿Está seguro?',
            text: "No podrás revertir esto",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: '¡Si, borralo!'
        }).then(async (result) => {
            if (result.isConfirmed) {
                await eliminarCategoriaFirebase(IdCategoria);
                obtener();
                Swal.fire(
                    'Eliminado',
                    'La Categoria ha sido eliminada.',
                    'success'
                )
            }
        })

    }


    return (
        <div className="container ">
            <h3 className="mt-2 mb-2 text-center">
                Listado de Categorías
            </h3>
            <table className="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">Nombre Categoría</th>
                        <th scope="col">Descripción</th>
                        <th scope="col">Editar</th>
                        <th scope="col">Eliminar</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        categorias.map((categoria, key) => {
                            return (
                                <tr key={categoria.id}>
                                    <th scope="row">{++key}</th>
                                    <td>
                                        {categoria.name}
                                    </td>
                                    <td>
                                        {categoria.description}
                                    </td>
                                    <td>
                                        <Link to={`/editar-categorias/${categoria.id}`} className="btn btn-sm btn-success">Editar</Link>
                                    </td>
                                    <td>
                                        <button onClick={() => eliminarCategoria(categoria.id)} type="button" className="btn btn-sm btn-danger">Eliminar</button>
                                    </td>
                                </tr>

                            )
                        })
                    }

                </tbody>
            </table>
        </div>
    )
}

export default Categorias
