import React, { useState } from 'react';
import { LoginFirebase } from '../services/service';
import '../styles/globalStyles.css';
import { useHistory } from 'react-router-dom'
import { AuthContext } from '../auth/AuthContext';
import { types } from '../types/types';

const Login = () => {
    const history = useHistory();
    const [user, setUser] = useState({
        email: 'admin@allinone.com',
        password: 'asd.456'
    });
    const [error, setError] = useState('')
    const { email, password } = user;

   
    const handleInputChange = (e) => {
        const { value } = e.target;

        setUser({
            ...user,
            [e.target.name]: value
        });
    }

    const handleClickLogin = async () => {
        const response = await LoginFirebase(email, password);


        if (response.code) {
            setError('Credenciales invalidas');
        } else {
            const { user } = response;
            const { uid } = user;

            localStorage.setItem('uid', uid);
            history.push('/resumen');
        }

    }

    return (
        <div className="w-100 h-100 contenedor-100 bg-light">
            <br></br><br></br><br></br><br></br><br></br>
            <div className="d-flex justify-content-center rounded">
                <div className="d-flex align-items-center bg-white col-6 card">

                    <div className="row pb-5 pt-5">
                        <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                            <img className="img-fluid" src="https://trello-attachments.s3.amazonaws.com/60a1e32ee85c2253ce8c2ba3/60beff0b82b0777a556d7040/ac2d26cece7172c02a2662ccfca4ab85/aio_logins.png" />
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                            <form>
                                <div className="form-group">
                                    <label htmlFor="exampleInputEmail1">Correo</label>
                                    <input onChange={handleInputChange} value={email} name="email" type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Ingrese su correo" />

                                </div>
                                <div className="form-group">
                                    <label htmlFor="exampleInputPassword1">Contraseña</label>
                                    <input name="password" value={password} onChange={handleInputChange} type="password" className="form-control" id="exampleInputPassword1" placeholder="Contraseña" />
                                </div>

                                {
                                    error.length > 0 && <p className="text-center text-danger" >{error}</p>
                                }

                                <button onClick={handleClickLogin} type="button" className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color:'white'}}>Iniciar Sesión</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}




export default Login
