import React, { useEffect, useState } from 'react';
import { obtenerBrandsFirebase, obtenerCategoriaFirebase, obtenerProductosFirebase, obtenerUsuarios, obtenerUsuariosFirebase } from '../services/service';


const Grafica = () => {


    const [users, setUsers] = useState(0);
    const [productos, setProdcutos] = useState(0);
    const [categorias, setCategorias] = useState(0);
    const [marcas, setMarcas] = useState(0);


    const [ordenes, setOrdenes] = useState(0);

    useEffect(() => {
        getData();
        obtenerNumeroProductos();
        obtenerNumeroCategorias();
        obtenerNumerosMarcas();

    }, [])

    const getData = async () => {
        let data = [];
        const response = await obtenerUsuariosFirebase();

        response.forEach(element => {
            data.push({
                id: element.id,
                ...element.data()
            })
        });

        setUsers(data.length);
    }

    const obtenerNumeroProductos = async () => {
        let data = [];
        const response = await obtenerProductosFirebase();

        response.forEach(element => {
            data.push({
                id: element.id,
                ...element.data()
            })
        });

        setProdcutos(data.length);
    }

    const obtenerNumeroCategorias = async () => {
        let data = [];
        const response = await obtenerCategoriaFirebase();

        response.forEach(element => {
            data.push({
                id: element.id,
                ...element.data()
            })
        });

        setCategorias(data.length);
    }

    const obtenerNumerosMarcas = async () => {
        let data = [];
        const response = await obtenerBrandsFirebase();

        response.forEach(element => {
            data.push({
                id: element.id,
                ...element.data()
            })
        });

        setMarcas(data.length);
    }


    return (
        <div className='container'>
            <br></br>
              <p className="h3 text-center">
                    Resumen de Datos 
                </p>
                <br></br>
            <div className='row'>
                <div className='col-md-4'>
                    <div className="card text-white bg-primary mb-3">
                        <div className="card-header">Usuarios</div>
                        <div className="card-body">
                            <h5 className="card-title">Cantidad Registrados</h5>
                            <p className="card-text">{users}</p>
                        </div>
                    </div>
                    <div className="card text-white mb-3" style={{ backgroundColor: '#4caf50' }} >
                        <div className="card-header">Marcas</div>
                        <div className="card-body">
                            <h5 className="card-title"> Cantidad Marcas Disponibles</h5>
                            <p className="card-text">{marcas}</p>
                        </div>
                    </div>
                </div>
                <div className='col-md-4'>
                    <div className="card text-white mb-3" style={{ backgroundColor: '#26c6da' }} >
                        <div className="card-header">Productos</div>
                        <div className="card-body">
                            <h5 className="card-title">Cantidad Productos Disponibles</h5>
                            <p className="card-text">{productos}</p>
                        </div>
                    </div>
                </div>
                <div className='col-md-4'>
                    <div className="card text-white mb-3" style={{ backgroundColor: '#ef5350' }} >
                        <div className="card-header">Categorías</div>
                        <div className="card-body">
                            <h5 className="card-title">Cantidad Categorías Disponibles</h5>
                            <p className="card-text">{categorias}</p>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default Grafica
