import React, { useState } from 'react';
import { guardarCategoriaFirebase } from '../services/service';
import Swal from 'sweetalert';
import { useHistory } from 'react-router-dom';

const CrearCategoria = () => {
    const history = useHistory();
    const [alerta, setAlerta] = useState(true);
    const [categories, setcategories] = useState({
        name: '',
        description: ''
    });

    //const [validar, setValidar] = useState(false)
    const { name, description } = categories;

    const handleInputChange = (e) => {

        setcategories({
            ...categories,
            [e.target.name]: e.target.value
        })
        setAlerta(true);

        /*   if (name.length > 0 && description.length > 0) {
               setValidar(true);
           } else {
               setValidar(false);
   
           }*/

    }

    const btnEnviar = async () => {
        if (name.trim().length > 0 && description.trim().length > 0) {
            await guardarCategoriaFirebase(categories);
            Swal('Categoría Creada  con Exito!', '', 'success').then((value) => {
                if (value) {
                    history.push('/categories')
                }
            });
        } else {
            setAlerta(false);
        }
    }


    return (
        <div className="w-100 h-100 contenedor-100 bg-light">
            <br></br>
            <div>
                <p className="h3 text-center">
                    Agregar Nueva Categoría
                </p>
            </div>
            <br></br>
            <div className="d-flex justify-content-center">
                <div className="d-flex align-items-center bg-white col-6 card">
                    <div className="row pb-5 pt-5">
                        <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                            <img className="img-fluid" alt="img" src="https://res.cloudinary.com/unahvs/image/upload/v1629397172/nuevas/Categorias_oy73xf.png" />
                        </div>
                        <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                            <div className="form-group ">
                                <label>Nombre</label>
                                <input onChange={handleInputChange} name="name" value={name} type="email" className="form-control" />
                            </div>
                            <div className="form-group">
                                <label>Descripción</label>
                                <textarea onChange={handleInputChange} name="description" value={description} type="text" className="form-control"></textarea>
                            </div>
                            {
                                alerta == false && <div className="alert alert-danger is-invalid" role="alert">
                                    Todos los campos son requeridos
                                </div>
                            }
                            <button onClick={btnEnviar} type="button" className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color: 'white' }}>Guardar</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CrearCategoria
