import React, { useEffect, useState } from 'react';
import '../styles/globalStyles.css';
import Swal from 'sweetalert';
import { useHistory } from 'react-router-dom';
import { guardarProductosFirebase, obtenerBrandsFirebase, obtenerCategoriaFirebase, subirImagen, } from '../services/service';
import moment from 'moment';
//import FileUploader from "react-firebase-file-uploader";

const CrearProducto = () => {
    const history = useHistory();
    //const [urlImagen, setUrlImagen] = useState('');
    //const [progreso, setProgreso] = useState(0);
    const [imgSubida, setImgSubida] = useState(false);
    const [categorias, setCategorias] = useState([]);
    const [alerta, setAlerta] = useState(true);
    const [marcas, setMarcas] = useState([]);
    const [products, setProducts] = useState({
        name: '',
        description: '',
        price: '',
        photoURL: '',
        brand: '',
        category: '',
        qualification: '',
        createdAt: moment().format('YYYY-MM-DD'),
        updateAt: ''
    });

    const { name, description, price, qualification, photoURL, brand, category } = products;

    const handleInputChange = (e) => {
        const { value } = e.target;

        setProducts({
            ...products,
            [e.target.name]: value
        });
        setAlerta(true);
    }


    const agregarProducto = async () => {
        if (name.trim().length > 0 && description.trim().length > 0 && price.trim().length > 0 && photoURL.trim().length > 0 && brand.trim().length > 0 && category.trim().length > 0 && qualification.trim().length > 0) {

            const resp = await guardarProductosFirebase(products);
            Swal('Producto Guardado con Exito!', '', 'success').then((value) => {
                if (value) {
                    history.push('/products');
                }
            });


        } else {
            setAlerta(false);
        }
    }

    const getCategorias = async () => {
        const resp = await obtenerCategoriaFirebase();
        let temp = [];

        resp.forEach(element => {
            temp.push({
                id: element.id,
                ...element.data()
            })
        });
        setCategorias(temp);
    }

    const getMarcas = async () => {
        const resp = await obtenerBrandsFirebase();
        let temp = [];

        resp.forEach(element => {
            temp.push({
                id: element.id,
                ...element.data()
            })
        });
        setMarcas(temp);
    }

    useEffect(() => {
        getCategorias();
        getMarcas();
    }, []);

    const handleSutmitImage = async (e) => {
        console.log(e.target.files[0]);

        let formData = new FormData();
        formData.append('upload_preset', 'galeria');
        formData.append('file', e.target.files[0]);

        const res = await subirImagen(formData);
        const body = await res.json();

        setProducts({
            ...products,
            photoURL: body.url
        });
        setImgSubida(true);
    }




    /*const handleUploadStart = () => {
        setSubiendo(true);
        setProgreso(0);
    
    }
    const handleUploadError = (error) => {
        setSubiendo(false);
        console.log(error)
    
    }
    const handleUploadSuccess = async (nombre) => {
        setProgreso(100);
        setSubiendo(false);
    
        const url = await firebase.storage.ref('productos').child(nombre).getDownloadURL();
    
        console.log(url);
    }
    const handleProgress = (progress) => {
        setProgreso(progress);
    }**/

    return (
        <div className="container" >
            <br></br>
            <div>
                <p className="h3 text-center">
                    Agregar Nuevo Producto
                </p>
            </div>

            <div className="row p-3">
                <div className="col-lg-6 col-md-6 col-sm-12 col-12 ">
                    <div>
                        <div className="form-group">
                            <label >Nombre</label>
                            <input name="name" value={name} onChange={handleInputChange} type="name" className="form-control" placeholder="Ingrese nombre del producto" />

                        </div>
                        <div className="form-group">
                            <label >Descripción</label>
                            <textarea cols="5" name="description" value={description} onChange={handleInputChange} className="form-control" placeholder="Ingrese una descripción" ></textarea>
                        </div>
                        <div className="form-group">
                            <label >Calificación</label>
                            <input name="qualification" value={qualification} onChange={handleInputChange} type="number" className="form-control" placeholder="Calificación" />

                        </div>
                        <button type="button" onClick={agregarProducto} className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color: 'white' }}> Guardar Producto</button>
                        
                       
                    </div>
                    <br></br>
                    {
                        alerta == false && <div className="alert alert-danger is-invalid " role="alert">
                            Todos los campos son requeridos
                        </div>
                    }
                </div>
                <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                    <div>
                        <div className="form-group">
                            <label >Precio</label>
                            <input type="number" name="price" value={price} onChange={handleInputChange} className="form-control" aria-describedby="emailHelp" placeholder="Ingrese el Precio" />

                        </div>
                        <div className="form-group">
                            <label >Marca</label>
                            <select defaultValue="0" name="brand" className="form-control" onChange={handleInputChange} >
                                <option value="0" > Seleccione una Marca </option>
                                {
                                    marcas.map(marca => {
                                        return (
                                            <option key={marca.id} value={marca.name} > {marca.name} </option>
                                        )
                                    })
                                }
                            </select>
                        </div>
                        <div className="form-group">
                            <label >Categoría</label>
                            <select name="category" defaultValue="0" className="form-control" onChange={handleInputChange} >
                                <option value="0" > Seleccione una Categoría </option>

                                {
                                    categorias.map(categoria => {
                                        return (
                                            <option key={categoria.id} value={categoria.name} > {categoria.name} </option>
                                        )
                                    })
                                }
                            </select>
                            <label >Imagen</label>
                        <div className="custom-file">
                            <input onChange={handleSutmitImage} type="file" className="custom-file-input" id="customFileLang" lang="es" />
                            <label className="custom-file-label" for="customFileLang">Seleccionar Fotografía</label>
                            {
                                imgSubida && <p>Imagen subida correctamente</p>
                            }
                        </div>
                            

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CrearProducto
