import React, { useEffect, useState } from 'react';
import '../styles/globalStyles.css';
import { useParams, useHistory } from 'react-router-dom';
import { obtenerMarcaPorIdFirebase, actualizarMarcaPorIdFirebase } from '../services/service';
import Swal from 'sweetalert';

const EditarMarca = () => {

    const { id } = useParams();
    const history = useHistory();

    const [alerta, setAlerta] = useState(true);
    const [marca, setMarca] = useState({
        id: '',
        name: '',
        description: '',
    });

    const { name, description } = marca;

    const handleInputChange = (e) => {
        setMarca({
            ...marca,
            [e.target.name]: e.target.value
        });
        setAlerta(true);
    }

    useEffect(() => {
        obtenerMarca();
    }, []);


    const obtenerMarca = async () => {
        const res = await obtenerMarcaPorIdFirebase(id);

        let data = {
            ...res.data()
        }

        let { name, description } = data;

        setMarca({ id, name, description });
    }

    const actualizar = async () => {
        if (name.trim().length > 0 && description.trim().length > 0) {
            const res = await actualizarMarcaPorIdFirebase(id, { name, description });
            Swal('Marca editada con Exito!', '', 'success').then((value) => {
                if (value) {
                    history.push('/brands')
                }
            });
        } else {
            setAlerta(false);
        }

    }

    return (
        <div className="bg-light align-middle">
            <br></br>
            <div className="text-center ">
                <p className="h3 text-center">
                    Editar Marca
                </p>
            </div>
            <br></br>
            <div className="container h-100" style={{ height: '100vh' }}>
                <div className="row ">
                    <div className="col-12 col-sm-12 col-md-4"> </div>
                    <div className="col-12 col-sm-12 col-md-4 border p-3 bg-white">
                        <div>

                            <div className="form-group">
                                <label >Nombre</label>
                                <input name="name" type="name" value={name} onChange={handleInputChange} className="form-control" row="3" placeholder="Ingrese nombre de la Marca" />
                            </div>
                            <div className="form-group">
                                <label >Descripción</label>
                                <textarea name="description" value={description} onChange={handleInputChange} className="form-control" rows=""></textarea>
                            </div>
                            {
                                alerta == false && <div className="alert alert-danger is-invalid" role="alert">
                                    Todos los campos son requeridos
                                </div>
                            }
                            <button onClick={actualizar} type="button" className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color: 'white' }}>Actualizar Marca</button>
                        </div>
                    </div>
                    <div className="col-12 col-sm-12 col-md-4"> </div>

                </div>

            </div>


        </div>
    )
}

export default EditarMarca
