import React, { useEffect, useState } from 'react';
import '../styles/globalStyles.css';
import { useParams, useHistory } from 'react-router-dom';
import { obtenerProductosPorIdFirebase, actualizarProductosPorIdFirebase } from '../services/service';
import Swal from 'sweetalert';

const EditarProducto = () => {
    const { id } = useParams();
    const history = useHistory();

    const [alerta, setAlerta] = useState(true);
    const [producto, setProducto] = useState({
        id: '',
        name: '',
        description: '',
        price: '',
        photoURL: '',
        brand: '',
        category: '',
        qualification: ''
    });

    const { name, description, price, photoURL, brand, category, qualification } = producto;

    const handleInputChange = (e) => {
        setProducto({
            ...producto,
            [e.target.name]: e.target.value
        });
        setAlerta(true);
    }

    useEffect(() => {
        obtenerProducto();
    }, []);


    const obtenerProducto = async () => {
        const res = await obtenerProductosPorIdFirebase(id);

        let data = {
            ...res.data()
        }

        let { name, description, price, photoURL, brand, category, qualification } = data;

        setProducto({ id, name, description, price, photoURL, brand, category, qualification });
    }

    const actualizar = async () => {
        console.log(producto);

        if (name.trim().length > 0 && description.trim().length > 0 && price.trim().length > 0 && brand.trim().length > 0 && category.trim().length > 0 && qualification.trim().length > 0) {
            await actualizarProductosPorIdFirebase(id, { name, description, price, brand, category, qualification });

            Swal('Producto editado con Exito!', '', 'success').then((value) => {
                if (value) {
                    history.push('/products');
                }
            });
        } else {
            setAlerta(false);
        }

    }


    return (
        <div className="container" >
            <br></br>
            <div>
                <p className="h3 text-center">
                    Editar Producto
                </p>
            </div>

            <div className="row p-3">
                <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                    <div>
                        <div className="form-group">
                            <label >Nombre</label>
                            <input name="name" type="name" value={name} onChange={handleInputChange} className="form-control" row="3" placeholder="Ingrese nombre del Producto" />

                        </div>
                        <div className="form-group">
                            <label >Descripción</label>
                            <textarea cols="5" name="description" value={description} onChange={handleInputChange} className="form-control" placeholder="Ingrese una descripción" ></textarea>
                        </div>
                        {
                            alerta == false && <div className="alert alert-danger is-invalid" role="alert">
                                Todos los campos son requeridos
                            </div>
                        }
                        <button onClick={actualizar} type="button" className="btn btn-sm btn-block mt-2" style={{ backgroundColor: '#f64d60', color:'white'}}>Actualizar Producto</button>
                    </div>
                </div>
                <div className="col-lg-6 col-md-6 col-sm-12 col-12">
                    <div>
                        <div className="form-group">
                            <label >Precio</label>
                            <input type="number" name="price" value={price} onChange={handleInputChange} className="form-control" aria-describedby="emailHelp" placeholder="Ingrese el Precio" />

                        </div>
                        <div className="form-group">
                            <label >Marca</label>
                            <input type="text" name="brand" value={brand} onChange={handleInputChange} className="form-control" placeholder="Ingrese la Marca" />
                        </div>
                        <div className="form-group">
                            <label >Categoría</label>
                            <input type="text" name="category" value={category} onChange={handleInputChange} className="form-control" placeholder="Elija la Categoria" />
                        </div>

                    </div>
                </div>
            </div>
            
        </div>
    )
}

export default EditarProducto
