import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { obtenerProductosFirebase, eliminarProductoFirebase, obtenerProductosPorIdFirebase } from '../services/service';
import Swal from 'sweetalert2'


const Productos = () => {
    const [productos, setProductos] = useState([]);
    const noImage = 'https://upload.wikimedia.org/wikipedia/commons/1/14/No_Image_Available.jpg';
    const [producto, setProducto] = useState({
        name: '',
        description: '',
    });

    const { name, description } = producto;

    useEffect(() => {
        obtenerProductos()
    }, [])


    const obtenerProductos = async () => {
        const temp = [];
        const resp = await obtenerProductosFirebase();

        resp.forEach(element => {
            temp.push({
                id: element.id,
                ...element.data()
            })
        });
        setProductos(temp);
    }

    const seleccionarProducto = async (id) => {
        const resp = await obtenerProductosPorIdFirebase(id);

        let data = {
            ...resp.data()
        }

        let { name, description } = data;

        setProducto({
            name,
            description
        });

    }

    const eliminarProducto = async (IdProducto) => {
        Swal.fire({
            title: '¿Está seguro?',
            text: "No podrás revertir esto",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: '¡Si, borralo!'
        }).then(async (result) => {
            if (result.isConfirmed) {
                await eliminarProductoFirebase(IdProducto);
                obtenerProductos();
                Swal.fire(
                    'Eliminado',
                    'El Producto ha sido eliminado.',
                    'success'
                )
            }
        })

    }

    return (


        <div className="container ">
            <h3 className="mt-2 mb-2 text-center">
                Listado de Productos
            </h3>

            <table className="table table-striped">
                <thead>
                    <tr>
                        <th scope="col">Imagen</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Precio</th>
                        <th scope="col">Marca </th>
                        <th scope="col">Categoría</th>
                        <th scope="col">Calificación</th>
                        <th scope="col">Editar</th>
                        <th scope="col">Eliminar</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        productos.map((producto) => {
                            return (
                                <tr key={producto.id}>
                                    <th scope="row">
                                        <button onClick={() => seleccionarProducto(producto.id)} type="button" className="btn" data-toggle="modal" data-target="#exampleModal">
                                            <img src={producto.photoURL || noImage} alt="img" className="rounded-circle" style={{ width: 50, height: 50 }} />
                                        </button>
                                    </th>
                                    <td>
                                        {producto.name}
                                    </td>
                                    <td>
                                        {producto.price}
                                    </td>
                                    <td>
                                        {producto.brand}
                                    </td>
                                    <td>
                                        {producto.category}
                                    </td>
                                    <td>
                                        {producto.qualification}
                                    </td>
                                    <td>
                                        <Link to={`/editar-productos/${producto.id}`} className="btn btn-sm btn-success">Editar</Link>
                                    </td>
                                    <td>
                                        <button onClick={() => eliminarProducto(producto.id)} type="button" className="btn btn-sm btn-danger">Eliminar</button>
                                    </td>
                                </tr>

                            )
                        })
                    }

                </tbody>

                <div>

                    <div className="modal fade" id="exampleModal" tabIndex={-1} role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="exampleModalLabel">{name}</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">×</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <p className="text-sm text-muted">
                                        {description}

                                    </p>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Cerrar</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </table>

        </div>
    )
}

export default Productos



